const strHtmInstall='<span style="color: #FF00FF">打印控件未安装!点击这里<a href="install_lodop32.exe" target="_self">执行安装</a>,安装后请刷新页面或重新进入。</span>';
const strHtmUpdate='<span style="color: #FF00FF">打印控件需要升级!点击这里<a href="install_lodop32.exe" target="_self">执行升级</a>,升级后请重新进入。</span>';
const strHtm64_Install='<span style="color: #FF00FF">打印控件未安装!点击这里<a href="install_lodop64.exe" target="_self">执行安装</a>,安装后请刷新页面或重新进入。</span>';
const strHtm64_Update='<span style="color: #FF00FF">打印控件需要升级!点击这里<a href="install_lodop64.exe" target="_self">执行升级</a>,升级后请重新进入。</span>';
const strHtmFireFox='<span style="color: #FF00FF">（注意：如曾安装过Lodop旧版附件npActiveXPLugin,请在【工具】->【附加组件】->【扩展】中先卸它）</span>';
const strHtmChrome='<span style="color: #FF00FF">(如果此前正常，仅因浏览器升级或重安装而出问题，需重新执行以上安装）</span>';
const strCLodopInstall='<span style="color: #FF00FF">CLodop云打印服务(localhost本地)未安装启动!点击这里<a href="CLodop_Setup_for_Win32NT.exe" target="_self">执行安装</a>,安装后请刷新页面。</span>';
const strCLodopUpdate='<span style="color: #FF00FF">CLodop云打印服务需升级!点击这里<a href="CLodop_Setup_for_Win32NT.exe" target="_self">执行升级</a>,升级后请刷新页面。</span>';
const emptyFunction=()=>{};
const render=(str)=>{
    let div=document.createElement('div');
    div.innerHTML=str;
    div.setAttribute('style','line-height:30px;background-color:#fff;border-bottom:1px solid #eee;padding:0 30px;color:#fff;position:fixed;left:0;top:0;width:100%;');
    let button=document.createElement('div');
    const buttonText=document.createTextNode('X');
    button.setAttribute('style','position:absolute;right:30px;width:20px;height:20px;border-radius:50%;background-color:#ffb100;top:5px;text-align:center;line-height:20px;cursor: pointer;');
    button.appendChild(buttonText);
    button.onclick=function () {
        button.onclick=null;
        button=null;
        div.parentNode.removeChild(div);
        div=null;
    };
    div.appendChild(button);
    document.body.appendChild(div)
};
/**
 * 基础信息
 * @type {object}
 */
export default {
    driveURL:['http://localhost:18000/CLodopfuncs.js?priority=0','http://localhost:8000/CLodopfuncs.js?priority=1'],
    beforeBuild:emptyFunction,
    afterBuild:emptyFunction,
    errorBuild:emptyFunction,
    mounted:emptyFunction,
    plugin:{
        notInstall:()=>render(strHtmInstall),
        notInstall64:()=>render(strHtm64_Install),
        notInstallC:()=>render(strCLodopInstall),
        notInstallFireFox:()=>render(strHtmFireFox),
        notInstallChrome:()=>render(strHtmChrome),
        hasUpdate:()=>render(strHtmUpdate),
        hasUpdate64:()=>render(strHtm64_Update),
        hasUpdateC:()=>render(strCLodopUpdate)
    }
};



